import { REHYDRATE } from 'redux-persist';

import {
    FAIL_RETURN_TFT_PROFILE,
    RETURN_TFT_PROFILE,
    START_TFT_PROFILE_REQUEST,
} from '../actions/types';

interface TFTInsightsReducer {
    requested: boolean,
    error: boolean,
    rank: string,

}

const INITIAL_STATE:TFTInsightsReducer = {
    requested: false,
    error: false,
    rank: '',
};

export default function (state = INITIAL_STATE, { type, payload }:any) {
    switch (type) {
        case REHYDRATE:
            if (payload && payload.tft) {
                return {
                    ...INITIAL_STATE,
                };
            }
            return state;
        case START_TFT_PROFILE_REQUEST:
            return {
                ...state,
                requested: false,
                error: false,
            }
        case RETURN_TFT_PROFILE:
            return {
                ...state,
                ...payload,
                requested: true,
                error: false,
            };
        case FAIL_RETURN_TFT_PROFILE:
            return{
                ...state,
                error: true,
            }
        default:
            return state;
    }
}
